'use client';

import { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { useSettings } from '../../../hooks/useSettings';

type Theme = 'light' | 'dark' | 'system';

interface ThemeContextType {
  theme: Theme;
  actualTheme: 'light' | 'dark'; // Ïã§Ï†ú Ï†ÅÏö©Îêú ÌÖåÎßà (system Ìï¥ÏÑù ÌõÑ)
  toggleTheme: () => void;
  setTheme: (theme: Theme) => void;
}

// ÌÖåÎßà Ïª®ÌÖçÏä§Ìä∏ ÏÉùÏÑ±
const ThemeContext = createContext<ThemeContextType | undefined>(undefined);

// ÌÖåÎßà Ïª®ÌÖçÏä§Ìä∏ ÌõÖ
export function useTheme() {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error('useTheme must be used within a ThemeProvider');
  }
  return context;
}

interface ThemeProviderProps {
  children: ReactNode;
}

export default function ThemeProvider({ children }: ThemeProviderProps) {
  const { settings, updateSetting } = useSettings();
  const [mounted, setMounted] = useState(false);
  const [actualTheme, setActualTheme] = useState<'light' | 'dark'>('light');

  // ÏãúÏä§ÌÖú ÌÖåÎßà Í∞êÏßÄ
  const getSystemTheme = (): 'light' | 'dark' => {
    if (typeof window === 'undefined') return 'light';
    return window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
  };

  // Ïã§Ï†ú ÌÖåÎßà Í≥ÑÏÇ∞
  const resolveTheme = (theme: Theme): 'light' | 'dark' => {
    if (theme === 'system') {
      return getSystemTheme();
    }
    return theme;
  };

  // ÌÖåÎßà Ï†ÅÏö©
  const applyTheme = (resolvedTheme: 'light' | 'dark') => {
    console.log('üé® ThemeProvider: ÌÖåÎßà Ï†ÅÏö©', resolvedTheme);
    
    if (typeof document !== 'undefined') {
      // HTML ÏöîÏÜåÏóê ÌÅ¥ÎûòÏä§ Ï†ÅÏö©
      const root = document.documentElement;
      const body = document.body;
      
      if (resolvedTheme === 'dark') {
        root.classList.add('dark', 'dark-mode');
        body.classList.add('dark', 'dark-mode');
        root.setAttribute('data-theme', 'dark');
        body.setAttribute('data-theme', 'dark');
      } else {
        root.classList.remove('dark', 'dark-mode');
        body.classList.remove('dark', 'dark-mode');
        root.setAttribute('data-theme', 'light');
        body.setAttribute('data-theme', 'light');
      }
      
      // Î©îÌÉÄ ÌÖåÎßà Ïª¨Îü¨ ÏÑ§Ï†ï
      const metaThemeColor = document.querySelector('meta[name="theme-color"]');
      if (metaThemeColor) {
        metaThemeColor.setAttribute('content', resolvedTheme === 'dark' ? '#1a1a1a' : '#ffffff');
      }
    }
    
    setActualTheme(resolvedTheme);
  };

  // Ï¥àÍ∏∞ ÌÖåÎßà ÏÑ§Ï†ï
  useEffect(() => {
    console.log('üîÑ ThemeProvider: Ï¥àÍ∏∞Ìôî ÏãúÏûë', { 
      settingsTheme: settings.theme, 
      darkMode: settings.darkMode 
    });
    
    // ÏÑ§Ï†ïÏóêÏÑú ÌÖåÎßà Í≤∞Ï†ï (darkMode ÏÑ§Ï†ïÏù¥ Ïö∞ÏÑ†)
    let initialTheme: Theme;
    if (settings.darkMode !== undefined) {
      // darkMode ÏÑ§Ï†ïÏù¥ ÏûàÏúºÎ©¥ Í∑∏Í≤ÉÏùÑ ÏÇ¨Ïö©
      initialTheme = settings.darkMode ? 'dark' : 'light';
    } else {
      // theme ÏÑ§Ï†ï ÏÇ¨Ïö©
      initialTheme = settings.theme || 'system';
    }
    
    console.log('üéØ ThemeProvider: Í≤∞Ï†ïÎêú Ï¥àÍ∏∞ ÌÖåÎßà', initialTheme);
    
    const resolved = resolveTheme(initialTheme);
    applyTheme(resolved);
    
    setMounted(true);
  }, [settings.theme, settings.darkMode]);

  // ÏãúÏä§ÌÖú ÌÖåÎßà Î≥ÄÍ≤Ω Í∞êÏßÄ
  useEffect(() => {
    if (!mounted || settings.theme !== 'system') return;
    
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    const handleChange = () => {
      console.log('üåô ThemeProvider: ÏãúÏä§ÌÖú ÌÖåÎßà Î≥ÄÍ≤Ω Í∞êÏßÄ');
      const resolved = resolveTheme('system');
      applyTheme(resolved);
    };
    
    mediaQuery.addEventListener('change', handleChange);
    return () => mediaQuery.removeEventListener('change', handleChange);
  }, [mounted, settings.theme]);

  // ÏÑ§Ï†ï Î≥ÄÍ≤Ω Ïãú ÌÖåÎßà ÏóÖÎç∞Ïù¥Ìä∏
  useEffect(() => {
    if (!mounted) return;
    
    const currentTheme = settings.darkMode !== undefined 
      ? (settings.darkMode ? 'dark' : 'light')
      : (settings.theme || 'system');
    
    const resolved = resolveTheme(currentTheme);
    if (resolved !== actualTheme) {
      console.log('‚ö° ThemeProvider: ÏÑ§Ï†ï Î≥ÄÍ≤ΩÏúºÎ°ú ÌÖåÎßà ÏóÖÎç∞Ïù¥Ìä∏', currentTheme, '->', resolved);
      applyTheme(resolved);
    }
  }, [settings.theme, settings.darkMode, actualTheme, mounted]);

  // ÌÖåÎßà Ï†ÑÌôò Ìï®Ïàò
  const toggleTheme = () => {
    console.log('üîÑ ThemeProvider: ÌÖåÎßà ÌÜ†Í∏Ä ÏãúÏûë', actualTheme);
    
    const newTheme = actualTheme === 'light' ? 'dark' : 'light';
    
    // ÏÑ§Ï†ï ÏóÖÎç∞Ïù¥Ìä∏ (Ïö∞ÏÑ†ÏàúÏúÑ: darkMode > theme)
    if (settings.darkMode !== undefined) {
      updateSetting('darkMode', newTheme === 'dark');
    } else {
      updateSetting('theme', newTheme);
    }
    
    console.log('‚úÖ ThemeProvider: ÌÖåÎßà ÌÜ†Í∏Ä ÏôÑÎ£å', newTheme);
  };

  // ÌÖåÎßà ÏÑ§Ï†ï Ìï®Ïàò
  const setTheme = (theme: Theme) => {
    console.log('üé® ThemeProvider: ÌÖåÎßà ÏÑ§Ï†ï', theme);
    
    if (theme === 'system') {
      updateSetting('theme', 'system');
      // system Î™®ÎìúÏóêÏÑúÎäî darkModeÎäî falseÎ°ú ÏÑ§Ï†ï
      updateSetting('darkMode', false);
    } else {
      // Î™ÖÏãúÏ†Å ÌÖåÎßà ÏÑ§Ï†ï
      updateSetting('darkMode', theme === 'dark');
      updateSetting('theme', theme);
    }
  };

  const currentTheme: Theme = settings.darkMode !== undefined 
    ? (settings.darkMode ? 'dark' : 'light')
    : (settings.theme || 'system');

  // ÌïòÏù¥ÎìúÎ†àÏù¥ÏÖò Î¨∏Ï†ú Î∞©ÏßÄ
  if (!mounted) {
    return (
      <div suppressHydrationWarning>
        {children}
      </div>
    );
  }

  return (
    <ThemeContext.Provider value={{ 
      theme: currentTheme, 
      actualTheme, 
      toggleTheme, 
      setTheme 
    }}>
      <div 
        suppressHydrationWarning 
        className={actualTheme}
        data-theme={actualTheme}
      >
        {children}
      </div>
    </ThemeContext.Provider>
  );
}
