                      { value: 'fullscreen', label: 'Ï†ÑÏ≤¥ ÌôîÎ©¥', icon: Maximize2 },
                      { value: 'fullscreenAutoHide', label: 'Ï†ÑÏ≤¥ ÌôîÎ©¥ (ÏûêÎèô Ïà®ÍπÄ)', icon: Eye }
