'use client'

import React, { useState, useEffect, useCallback } from 'react'
import { MemoryStick, Cpu, HardDrive, Zap, RefreshCw, AlertTriangle } from 'lucide-react'

interface MemoryInfo {
  total: number
  used: number
  free: number
  percentage: number
}

interface MemoryData {
  main: MemoryInfo
  renderer: MemoryInfo
  gpu?: MemoryInfo
  system: MemoryInfo
  application?: MemoryInfo // Ïï†ÌîåÎ¶¨ÏºÄÏù¥ÏÖò Ï¥ù ÏÇ¨Ïö©Îüâ Ï∂îÍ∞Ä
  timestamp: number
}

interface MemoryMonitorProps {
  className?: string
  autoRefresh?: boolean
  refreshInterval?: number
}

export const MemoryMonitor: React.FC<MemoryMonitorProps> = ({ 
  className = '',
  autoRefresh = true,
  refreshInterval = 2000
}) => {
  const [memoryData, setMemoryData] = useState<MemoryData | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [history, setHistory] = useState<MemoryData[]>([])
  const [isGCRunning, setIsGCRunning] = useState(false)

  // Î©îÎ™®Î¶¨ Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
  const fetchMemoryInfo = useCallback(async () => {
    try {
      setIsLoading(true)
      setError(null)
      
      if (typeof window !== 'undefined' && window.electronAPI) {
        const response = await window.electronAPI.memory.getInfo()
        
        if (response.success && response.data) {
          const newData: MemoryData = {
            ...response.data,
            timestamp: Date.now()
          }
          
          setMemoryData(newData)
          setHistory(prev => {
            const updated = [...prev, newData]
            // ÏµúÍ∑º 60Í∞ú Îç∞Ïù¥ÌÑ∞Îßå Ïú†ÏßÄ (2Î∂ÑÍ∞Ñ Îç∞Ïù¥ÌÑ∞)
            return updated.slice(-60)
          })
        } else {
          setError(response.error || 'Î©îÎ™®Î¶¨ Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§')
        }
      }
    } catch (err) {
      console.error('Î©îÎ™®Î¶¨ Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:', err)
      setError(err instanceof Error ? err.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò')
    } finally {
      setIsLoading(false)
    }
  }, [])

  // Í∞ÄÎπÑÏßÄ Ïª¨Î†âÏÖò Ïã§Ìñâ
  const runGarbageCollection = useCallback(async () => {
    try {
      setIsGCRunning(true)
      
      if (typeof window !== 'undefined' && window.electronAPI) {
        const response = await window.electronAPI.memory.cleanup()
        
        if (response.success) {
          // GC ÌõÑ Î©îÎ™®Î¶¨ Ï†ïÎ≥¥ ÏÉàÎ°úÍ≥†Ïπ®
          setTimeout(fetchMemoryInfo, 1000)
        } else {
          setError(response.error || 'GC Ïã§Ìñâ Ïã§Ìå®')
        }
      }
    } catch (err) {
      console.error('GC Ïã§Ìñâ Ïã§Ìå®:', err)
      setError(err instanceof Error ? err.message : 'GC Ïã§Ìñâ Ï§ë Ïò§Î•ò')
    } finally {
      setIsGCRunning(false)
    }
  }, [fetchMemoryInfo])

  // Î©îÎ™®Î¶¨ ÏµúÏ†ÅÌôî
  const optimizeMemory = useCallback(async () => {
    try {
      if (typeof window !== 'undefined' && window.electronAPI) {
        const response = await window.electronAPI.memory.optimize()
        
        if (response.success) {
          setTimeout(fetchMemoryInfo, 1000)
        } else {
          setError(response.error || 'Î©îÎ™®Î¶¨ ÏµúÏ†ÅÌôî Ïã§Ìå®')
        }
      }
    } catch (err) {
      console.error('Î©îÎ™®Î¶¨ ÏµúÏ†ÅÌôî Ïã§Ìå®:', err)
      setError(err instanceof Error ? err.message : 'Î©îÎ™®Î¶¨ ÏµúÏ†ÅÌôî Ï§ë Ïò§Î•ò')
    }
  }, [fetchMemoryInfo])

  // ÏûêÎèô ÏÉàÎ°úÍ≥†Ïπ® ÏÑ§Ï†ï
  useEffect(() => {
    fetchMemoryInfo() // Ï¥àÍ∏∞ Î°úÎìú
    
    if (autoRefresh) {
      const interval = setInterval(fetchMemoryInfo, refreshInterval)
      return () => clearInterval(interval)
    }
  }, [fetchMemoryInfo, autoRefresh, refreshInterval])

  // Î©îÎ™®Î¶¨ ÏÇ¨Ïö©ÎüâÏóê Îî∞Î•∏ ÏÉâÏÉÅ Í≤∞Ï†ï
  const getUsageColor = (percentage: number) => {
    if (percentage >= 85) return 'text-red-500'
    if (percentage >= 70) return 'text-yellow-500'
    return 'text-green-500'
  }

  const getProgressBarColor = (percentage: number) => {
    if (percentage >= 85) return 'bg-red-500'
    if (percentage >= 70) return 'bg-yellow-500'
    return 'bg-green-500'
  }

  // Î©îÎ™®Î¶¨ ÌÅ¨Í∏∞ Ìè¨Îß∑ÌåÖ (Ïù¥ÎØ∏ MB Îã®ÏúÑÎ°ú ÏûÖÎ†•Î∞õÏùå)
  const formatMemorySize = (megabytes: number) => {
    if (megabytes >= 1024) {
      return `${(megabytes / 1024).toFixed(1)} GB`
    }
    return `${megabytes.toFixed(1)} MB`
  }

  // Î©îÎ™®Î¶¨ Ï†ïÎ≥¥ Ïπ¥Îìú Ïª¥Ìè¨ÎÑåÌä∏
  const MemoryCard: React.FC<{
    title: string
    icon: React.ReactNode
    info: MemoryInfo
  }> = ({ title, icon, info }) => (
    <div className="bg-gray-50 dark:bg-gray-800 rounded-lg p-4 border border-gray-100 dark:border-gray-700">
      <div className="flex items-center gap-2 mb-3">
        {icon}
        <h4 className="font-medium text-gray-900 dark:text-white">{title}</h4>
      </div>
      
      <div className="space-y-2">
        <div className="flex justify-between text-sm">
          <span className="text-gray-600 dark:text-gray-400">ÏÇ¨Ïö©Îê®</span>
          <span className={`font-medium ${getUsageColor(info.percentage)}`}>
            {formatMemorySize(info.used)} ({info.percentage.toFixed(1)}%)
          </span>
        </div>
        
        <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
          <div 
            className={`h-2 rounded-full transition-all duration-300 ${getProgressBarColor(info.percentage)}`}
            style={{ width: `${Math.min(info.percentage, 100)}%` }}
          />
        </div>
        
        <div className="flex justify-between text-xs text-gray-500 dark:text-gray-400">
          <span>Ïó¨Ïú†: {formatMemorySize(info.free)}</span>
          <span>Ï†ÑÏ≤¥: {formatMemorySize(info.total)}</span>
        </div>
      </div>
    </div>
  )

  return (
    <div className={`bg-white dark:bg-gray-900 rounded-lg border border-gray-200 dark:border-gray-700 p-6 ${className}`}>
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center gap-2">
          <MemoryStick className="w-5 h-5 text-blue-500" />
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white">Î©îÎ™®Î¶¨ Î™®ÎãàÌÑ∞</h3>
          {isLoading && (
            <div className="animate-spin rounded-full h-4 w-4 border-2 border-blue-500 border-t-transparent"></div>
          )}
        </div>
        
        <div className="flex items-center gap-3">
          <button
            onClick={fetchMemoryInfo}
            disabled={isLoading}
            className={`
              relative inline-flex items-center justify-center
              w-10 h-6 rounded-full transition-all duration-300 cursor-pointer
              ${isLoading 
                ? 'bg-blue-500 opacity-60 cursor-not-allowed' 
                : 'bg-gray-300 dark:bg-gray-600 hover:bg-blue-400 dark:hover:bg-blue-500'
              }
              focus:outline-none focus:ring-0
            `}
            title="ÏÉàÎ°úÍ≥†Ïπ®"
            style={{ outline: 'none', boxShadow: 'none' }}
          >
            <RefreshCw className={`w-3 h-3 text-white transition-transform duration-300 ${isLoading ? 'animate-spin' : ''}`} />
          </button>
          
          <button
            onClick={runGarbageCollection}
            disabled={isGCRunning}
            className={`
              relative inline-flex items-center justify-center
              px-3 py-1.5 rounded-full text-xs font-medium transition-all duration-300 cursor-pointer
              ${isGCRunning 
                ? 'bg-blue-500 text-white opacity-60 cursor-not-allowed' 
                : 'bg-gray-300 dark:bg-gray-600 text-gray-700 dark:text-gray-200 hover:bg-blue-500 hover:text-white dark:hover:bg-blue-500'
              }
              focus:outline-none focus:ring-0
            `}
            title="Í∞ÄÎπÑÏßÄ Ïª¨Î†âÏÖò Ïã§Ìñâ"
            style={{ outline: 'none', boxShadow: 'none' }}
          >
            {isGCRunning ? 'Ïã§Ìñâ Ï§ë...' : 'GC'}
          </button>
          
          <button
            onClick={optimizeMemory}
            disabled={isLoading}
            className={`
              relative inline-flex items-center justify-center
              w-10 h-6 rounded-full transition-all duration-300 cursor-pointer
              ${isLoading 
                ? 'bg-green-500 opacity-60 cursor-not-allowed' 
                : 'bg-gray-300 dark:bg-gray-600 hover:bg-green-500 dark:hover:bg-green-500'
              }
              focus:outline-none focus:ring-0
            `}
            title="Î©îÎ™®Î¶¨ ÏµúÏ†ÅÌôî"
            style={{ outline: 'none', boxShadow: 'none' }}
          >
            <Zap className="w-3 h-3 text-white" />
          </button>
        </div>
      </div>

      {error && (
        <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg flex items-center gap-2">
          <AlertTriangle className="w-4 h-4 text-red-500" />
          <span className="text-sm text-red-700">{error}</span>
        </div>
      )}

      {memoryData ? (
        <div className="space-y-4">
          {/* Ïï†ÌîåÎ¶¨ÏºÄÏù¥ÏÖò Ï¥ù ÏÇ¨Ïö©Îüâ ÏöîÏïΩ (Îß® ÏúÑÏóê ÌëúÏãú) */}
          {memoryData.application && (
            <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-700 rounded-lg p-4">
              <div className="flex items-center gap-2 mb-3">
                <Zap className="w-5 h-5 text-blue-600 dark:text-blue-400" />
                <h4 className="font-semibold text-blue-900 dark:text-blue-200">Ïï†ÌîåÎ¶¨ÏºÄÏù¥ÏÖò Ï¥ù Î©îÎ™®Î¶¨ ÏÇ¨Ïö©Îüâ</h4>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-sm text-blue-700 dark:text-blue-300">
                  {formatMemorySize(memoryData.application.used)} / {formatMemorySize(memoryData.application.total)}
                </span>
                <span className={`text-lg font-bold ${getUsageColor(memoryData.application.percentage)}`}>
                  {memoryData.application.percentage.toFixed(1)}%
                </span>
              </div>
              <div className="w-full bg-blue-200 dark:bg-blue-800 rounded-full h-3 mt-2">
                <div 
                  className={`h-3 rounded-full transition-all duration-300 ${getProgressBarColor(memoryData.application.percentage)}`}
                  style={{ width: `${Math.min(memoryData.application.percentage, 100)}%` }}
                />
              </div>
            </div>
          )}

          {/* Î©îÎ™®Î¶¨ Ï†ïÎ≥¥ Ïπ¥ÎìúÎì§ */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <MemoryCard
              title="Î©îÏù∏ ÌîÑÎ°úÏÑ∏Ïä§"
              icon={<Cpu className="w-4 h-4 text-blue-500" />}
              info={memoryData.main}
            />
            
            <MemoryCard
              title="Î†åÎçîÎü¨ ÌîÑÎ°úÏÑ∏Ïä§"
              icon={<MemoryStick className="w-4 h-4 text-green-500" />}
              info={memoryData.renderer}
            />
            
            {memoryData.gpu && (
              <MemoryCard
                title="GPU Î©îÎ™®Î¶¨"
                icon={<Zap className="w-4 h-4 text-purple-500" />}
                info={memoryData.gpu}
              />
            )}
            
            <MemoryCard
              title="ÏãúÏä§ÌÖú Î©îÎ™®Î¶¨"
              icon={<HardDrive className="w-4 h-4 text-orange-500" />}
              info={memoryData.system}
            />
          </div>

          {/* ÏÉÅÏÑ∏ Î©îÎ™®Î¶¨ ÏÇ¨Ïö©Îüâ Ï†ïÎ≥¥ */}
          <div className="bg-gray-50 dark:bg-gray-800 rounded-lg p-4 border border-gray-100 dark:border-gray-700">
            <h4 className="font-medium text-gray-900 dark:text-white mb-3">Î©îÎ™®Î¶¨ ÏÇ¨Ïö©Îüâ ÏÉÅÏÑ∏</h4>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">Î©îÏù∏ ÌîÑÎ°úÏÑ∏Ïä§</span>
                  <span className="font-medium dark:text-gray-200">
                    {formatMemorySize(memoryData.main.used)} / {formatMemorySize(memoryData.main.total)}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">Î†åÎçîÎü¨ ÌîÑÎ°úÏÑ∏Ïä§</span>
                  <span className="font-medium dark:text-gray-200">
                    {formatMemorySize(memoryData.renderer.used)} / {formatMemorySize(memoryData.renderer.total)}
                  </span>
                </div>
                {memoryData.application && (
                  <div className="flex justify-between font-semibold text-blue-700 dark:text-blue-300 border-t border-gray-200 dark:border-gray-600 pt-2">
                    <span>Ïï†ÌîåÎ¶¨ÏºÄÏù¥ÏÖò Ìï©Í≥Ñ</span>
                    <span>
                      {formatMemorySize(memoryData.application.used)} / {formatMemorySize(memoryData.application.total)}
                    </span>
                  </div>
                )}
              </div>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">ÏãúÏä§ÌÖú Ï¥ù Î©îÎ™®Î¶¨</span>
                  <span className="font-medium dark:text-gray-200">
                    {formatMemorySize(memoryData.system.total)}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">ÏãúÏä§ÌÖú ÏÇ¨Ïö© Ï§ë</span>
                  <span className="font-medium dark:text-gray-200">
                    {formatMemorySize(memoryData.system.used)}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600 dark:text-gray-400">ÏãúÏä§ÌÖú Ïó¨Ïú†</span>
                  <span className="font-medium text-green-600 dark:text-green-400">
                    {formatMemorySize(memoryData.system.free)}
                  </span>
                </div>
                {memoryData.gpu && (
                  <div className="flex justify-between border-t border-gray-200 dark:border-gray-600 pt-2">
                    <span className="text-gray-600 dark:text-gray-400">GPU Î©îÎ™®Î¶¨</span>
                    <span className="font-medium dark:text-gray-200">
                      {formatMemorySize(memoryData.gpu.used)} / {formatMemorySize(memoryData.gpu.total)}
                    </span>
                  </div>
                )}
              </div>
            </div>
          </div>

          {/* Î©îÎ™®Î¶¨ ÏÇ¨Ïö©Îüâ Í≤ΩÍ≥† */}
          {(memoryData.application && memoryData.application.percentage > 85) || memoryData.system.percentage > 90 ? (
            <div className="bg-red-50 border border-red-200 rounded-lg p-3">
              <div className="flex items-center gap-2">
                <AlertTriangle className="w-4 h-4 text-red-600" />
                <span className="text-sm font-medium text-red-800">ÎÜíÏùÄ Î©îÎ™®Î¶¨ ÏÇ¨Ïö©Îüâ Í≤ΩÍ≥†</span>
              </div>
              <p className="text-sm text-red-700 mt-1">
                {memoryData.application && memoryData.application.percentage > 85 && 
                  "Ïï†ÌîåÎ¶¨ÏºÄÏù¥ÏÖò Î©îÎ™®Î¶¨ ÏÇ¨Ïö©ÎüâÏù¥ ÎÜíÏäµÎãàÎã§. "}
                {memoryData.system.percentage > 90 && 
                  "ÏãúÏä§ÌÖú Î©îÎ™®Î¶¨ ÏÇ¨Ïö©ÎüâÏù¥ ÎÜíÏäµÎãàÎã§. "}
                ÏÑ±Îä• Ï†ÄÌïòÎ•º Î∞©ÏßÄÌïòÍ∏∞ ÏúÑÌï¥ Î©îÎ™®Î¶¨ ÏµúÏ†ÅÌôîÎ•º Ïã§ÌñâÌïòÍ±∞ÎÇò Î∂àÌïÑÏöîÌïú ÌîÑÎ°úÍ∑∏Îû®ÏùÑ Ï¢ÖÎ£åÌïòÎäî Í≤ÉÏùÑ Í∂åÏû•Ìï©ÎãàÎã§.
              </p>
            </div>
          ) : (memoryData.application && memoryData.application.percentage > 70) || memoryData.system.percentage > 80 ? (
            <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-3">
              <div className="flex items-center gap-2">
                <AlertTriangle className="w-4 h-4 text-yellow-600" />
                <span className="text-sm font-medium text-yellow-800">Î©îÎ™®Î¶¨ ÏÇ¨Ïö©Îüâ Ï£ºÏùò</span>
              </div>
              <p className="text-sm text-yellow-700 mt-1">
                Î©îÎ™®Î¶¨ ÏÇ¨Ïö©ÎüâÏù¥ Ï¶ùÍ∞ÄÌïòÍ≥† ÏûàÏäµÎãàÎã§. Î©îÎ™®Î¶¨ Ï†ïÎ¶¨Î•º Ïã§ÌñâÌïòÏó¨ ÏÑ±Îä•ÏùÑ Í∞úÏÑ†Ìï† Ïàò ÏûàÏäµÎãàÎã§.
              </p>
            </div>
          ) : null}
        </div>
      ) : (
        <div className="text-center py-8 text-gray-500 dark:text-gray-400">
          <MemoryStick className="w-12 h-12 mx-auto mb-3 text-gray-300 dark:text-gray-600" />
          <p>Î©îÎ™®Î¶¨ Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ë...</p>
        </div>
      )}
    </div>
  )
}

export default MemoryMonitor
