// Loop Ïï± CSS Î≥ÄÏàò Ï¥àÍ∏∞Ìôî Î∞è Ïä§ÌÉÄÏùº Í∞ïÏ†ú Ï†ÅÏö©
const initStyles = () => {
  // CSS Î≥ÄÏàòÍ∞Ä Ï†ÅÏö©ÎêòÏóàÎäîÏßÄ ÌôïÏù∏ÌïòÎêò, ThemeProviderÍ∞Ä Í¥ÄÎ¶¨ÌïòÎäî ÌÖåÎßàÎäî Í±¥ÎìúÎ¶¨ÏßÄ ÏïäÏùå
  let style = document.getElementById('loop-init-styles');
  
  // Ïù¥ÎØ∏ Ïä§ÌÉÄÏùºÏù¥ ÏûàÏúºÎ©¥ Ï†úÍ±∞ÌïòÏßÄ ÏïäÍ≥† ÏóÖÎç∞Ïù¥Ìä∏Îßå Ìï®
  if (!style) {
    style = document.createElement('style');
    style.id = 'loop-init-styles';
    document.head.appendChild(style);
  }
  
  style.textContent = `
    /* Loop Ïï± Í∏∞Î≥∏ CSS Î≥ÄÏàò - ThemeProviderÎ≥¥Îã§ ÎÇÆÏùÄ Ïö∞ÏÑ†ÏàúÏúÑ */
    :root {
      --background-color: #f9f9f9;
      --text-color: #333;
      --primary-color: #0070f3;
      --text-secondary: #666;
      --border-color: #e0e0e0;
      --card-bg: #ffffff;
      --header-bg: #ffffff;
      --footer-bg: #f0f0f0;
    }
    
    /* Îã§ÌÅ¨Î™®Îìú Î≥ÄÏàò Ï†ïÏùò - ThemeProviderÍ∞Ä .dark ÌÅ¥ÎûòÏä§Î•º Ï∂îÍ∞ÄÌïòÎ©¥ Ï†ÅÏö©Îê® */
    .dark, .dark-mode, [data-theme="dark"] {
      --background-color: #121212 !important;
      --text-color: #e0e0e0 !important;
      --text-secondary: #a0a0a0 !important;
      --border-color: #333 !important;
      --card-bg: #1e1e1e !important;
      --header-bg: #1e1e1e !important;
      --footer-bg: #121212 !important;
    }
    
    /* bodyÏóê CSS Î≥ÄÏàò Ï†ÅÏö© - transition Ìè¨Ìï® */
    body {
      background-color: var(--background-color) !important;
      color: var(--text-color) !important;
      transition: background-color 0.3s ease, color 0.3s ease;
    }
    
    /* Ïπ¥Îìú Î∞è Ï£ºÏöî Ïª¥Ìè¨ÎÑåÌä∏ÎèÑ Î≥ÄÏàò ÏÇ¨Ïö© */
    .bg-white, .bg-card {
      background-color: var(--card-bg) !important;
    }
    
    .text-gray-900, .text-black {
      color: var(--text-color) !important;
    }
    
    .text-gray-600, .text-gray-500 {
      color: var(--text-secondary) !important;
    }
  `;
  
  // Í∏∞Î≥∏ Ìè∞Ìä∏ Î°úÎìú
  if (!document.getElementById('loop-fonts')) {
    const fontLink = document.createElement('link');
    fontLink.id = 'loop-fonts';
    fontLink.rel = 'stylesheet';
    fontLink.href = '/assets/fonts/font.css';
    document.head.appendChild(fontLink);
  }
  
  // Ï†ÑÏûêÎ¨∏ÏÑú Ïä§ÌÉÄÏùº Î°úÎìú
  if (!document.getElementById('loop-electron-styles')) {
    const electronStyleLink = document.createElement('link');
    electronStyleLink.id = 'loop-electron-styles';
    electronStyleLink.rel = 'stylesheet';
    electronStyleLink.href = '/assets/styles/electron-styles.css';
    document.head.appendChild(electronStyleLink);
  }
  
  console.log('Loop CSS Î≥ÄÏàò Î∞è Ïä§ÌÉÄÏùº Ï¥àÍ∏∞Ìôî ÏôÑÎ£å');
  
  // ÏùºÏ†ï ÏãúÍ∞Ñ ÌõÑ DOMÏóê Ïä§ÌÉÄÏùºÏù¥ Ï†úÎåÄÎ°ú Ï†ÅÏö©ÎêòÏóàÎäîÏßÄ ÌôïÏù∏
  setTimeout(() => {
    const bodyBg = getComputedStyle(document.body).backgroundColor;
    const isDark = document.documentElement.classList.contains('dark') || 
                   document.documentElement.classList.contains('dark-mode');
    console.log(`ÌòÑÏû¨ body Î∞∞Í≤ΩÏÉâ: ${bodyBg}, Îã§ÌÅ¨Î™®Îìú: ${isDark}`);
  }, 1000);
};

export default initStyles;
