'use client';

import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { 
  NotificationItem, 
  NotificationContextType, 
  NotificationSettings,
  DEFAULT_NOTIFICATION_SETTINGS 
} from './types';

/**
 * ÏïåÎ¶º Ïª®ÌÖçÏä§Ìä∏ ÏÉùÏÑ±
 * Pub-Sub Ìå®ÌÑ¥ÏùÑ ÌôúÏö©Ìïú Í∏ÄÎ°úÎ≤å ÏïåÎ¶º Í¥ÄÎ¶¨
 */
const NotificationContext = createContext<NotificationContextType | undefined>(undefined);

interface NotificationProviderProps {
  children: React.ReactNode;
}

export function NotificationProvider({ children }: NotificationProviderProps) {
  const [notifications, setNotifications] = useState<NotificationItem[]>([]);
  const [settings, setSettings] = useState<NotificationSettings>(DEFAULT_NOTIFICATION_SETTINGS);

  // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú ÏÑ§Ï†ï Î°úÎìú
  useEffect(() => {
    const savedSettings = localStorage.getItem('notification-settings');
    if (savedSettings) {
      try {
        const parsed = JSON.parse(savedSettings);
        setSettings({ ...DEFAULT_NOTIFICATION_SETTINGS, ...parsed });
      } catch (error) {
        console.error('Failed to parse notification settings:', error);
      }
    }

    // Í∏∞Ï°¥ ÏïåÎ¶º Îç∞Ïù¥ÌÑ∞ ÎßàÏù¥Í∑∏Î†àÏù¥ÏÖò (MCP Î©îÎ™®Î¶¨ÏóêÏÑú ÌôïÏû•Îêú ÏÉòÌîå Îç∞Ïù¥ÌÑ∞)
    const sampleNotifications: NotificationItem[] = [
      {
        id: '1',
        type: 'success',
        title: 'MCP ÏÑúÎ≤Ñ Ïó∞Í≤∞',
        message: 'TaskManager, Supabase, MongoDB MCP ÏÑúÎ≤ÑÏôÄ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ïó∞Í≤∞ÎêòÏóàÏäµÎãàÎã§',
        timestamp: new Date(Date.now() - 5 * 60 * 1000),
        read: false,
        category: 'system',
        priority: 'medium'
      },
      {
        id: '2',
        type: 'info',
        title: 'Î©îÎ™®Î¶¨ Î™®ÎãàÌÑ∞ÎßÅ ÌôúÏÑ±Ìôî',
        message: 'Ïã§ÏãúÍ∞Ñ Î©îÎ™®Î¶¨ Î™®ÎãàÌÑ∞ÎßÅÏù¥ ÏãúÏûëÎêòÏóàÏäµÎãàÎã§. ÌòÑÏû¨ ÏÇ¨Ïö©Î•†: 62%',
        timestamp: new Date(Date.now() - 10 * 60 * 1000),
        read: false,
        category: 'memory',
        priority: 'low'
      },
      {
        id: '3',
        type: 'warning',
        title: 'CPU ÏÇ¨Ïö©Î•† ÎÜíÏùå',
        message: 'CPU ÏÇ¨Ïö©Î•†Ïù¥ 85%Î•º Ï¥àÍ≥ºÌñàÏäµÎãàÎã§. ÏùºÎ∂Ä ÏûëÏóÖÏùÑ Ï§ëÎã®ÌïòÎäî Í≤ÉÏùÑ Í∂åÏû•Ìï©ÎãàÎã§',
        timestamp: new Date(Date.now() - 15 * 60 * 1000),
        read: true,
        category: 'system',
        priority: 'high'
      },
      {
        id: '4',
        type: 'success',
        title: 'Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ ÎèôÍ∏∞Ìôî',
        message: 'Supabase Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ÏôÄ ÎèôÍ∏∞ÌôîÍ∞Ä ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§. 47Í∞úÏùò Î†àÏΩîÎìúÍ∞Ä ÏóÖÎç∞Ïù¥Ìä∏ÎêòÏóàÏäµÎãàÎã§',
        timestamp: new Date(Date.now() - 30 * 60 * 1000),
        read: false,
        category: 'system',
        priority: 'medium'
      },
      {
        id: '5',
        type: 'info',
        title: 'ÏÉàÎ°úÏö¥ ÏûëÏóÖ Ï∂îÍ∞Ä',
        message: 'TaskManagerÎ•º ÌÜµÌï¥ 5Í∞úÏùò ÏÉàÎ°úÏö¥ ÏûëÏóÖÏù¥ Ï∂îÍ∞ÄÎêòÏóàÏäµÎãàÎã§',
        timestamp: new Date(Date.now() - 45 * 60 * 1000),
        read: true,
        category: 'task',
        priority: 'low'
      },
      {
        id: '6',
        type: 'error',
        title: 'MongoDB Ïó∞Í≤∞ Ïò§Î•ò',
        message: 'MongoDB ÏÑúÎ≤ÑÏóê Ïó∞Í≤∞Ìï† Ïàò ÏóÜÏäµÎãàÎã§. ÎÑ§Ìä∏ÏõåÌÅ¨ ÏÉÅÌÉúÎ•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî',
        timestamp: new Date(Date.now() - 60 * 60 * 1000),
        read: false,
        category: 'system',
        priority: 'urgent'
      }
    ];
    
    setNotifications(sampleNotifications);
  }, []);

  // ÏÑ§Ï†ï Î≥ÄÍ≤Ω Ïãú Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•
  useEffect(() => {
    localStorage.setItem('notification-settings', JSON.stringify(settings));
  }, [settings]);

  // ÏïåÎ¶º Ï∂îÍ∞Ä
  const addNotification = useCallback((notification: Omit<NotificationItem, 'id' | 'timestamp'>) => {
    if (!settings.enabled) return;

    const newNotification: NotificationItem = {
      ...notification,
      id: Date.now().toString(),
      timestamp: new Date(),
      read: false
    };

    setNotifications(prev => [newNotification, ...prev]);

    // ÏÇ¨Ïö¥Îìú Ïû¨ÏÉù (ÏÑ§Ï†ïÏù¥ ÌôúÏÑ±ÌôîÎêú Í≤ΩÏö∞)
    if (settings.soundEnabled) {
      // Î∏åÎùºÏö∞Ï†Ä ÏïåÎ¶ºÏùå Ïû¨ÏÉù (Ïã§Ï†ú Íµ¨ÌòÑÏóêÏÑúÎäî Ïª§Ïä§ÌÖÄ ÏÇ¨Ïö¥Îìú ÌååÏùº ÏÇ¨Ïö© Í∞ÄÎä•)
      try {
        new Audio('data:audio/wav;base64,UklGRnoGAABXQVZFZm10IBAAAAABAAEAQB8AAEAfAAABAAgAZGF0YQoGAACBhYqFbF1fdJivrJBhNjVgodDbq2EcBj+a2/LDciUFLIHO8tiJNwgZaLvt559NEAxQp+PwtmMcBjiR1/LMeSwFJHfH8N2QQAoUXrTp66hVFApGn+LyvmwhBjiS1fPAeSsFJXfM7+KOTAYQW7LO').play();
      } catch (error) {
        console.log('Sound notification failed:', error);
      }
    }

    // ÏûêÎèô Ïà®ÍπÄ ÏÑ§Ï†ï
    if (settings.autoHide && notification.type !== 'error') {
      setTimeout(() => {
        removeNotification(newNotification.id);
      }, settings.autoHideDelay);
    }
  }, [settings]);

  // ÏïåÎ¶º Ï†úÍ±∞
  const removeNotification = useCallback((id: string) => {
    setNotifications(prev => prev.filter(notification => notification.id !== id));
  }, []);

  // ÏùΩÏùå Ï≤òÎ¶¨
  const markAsRead = useCallback((id: string) => {
    setNotifications(prev => 
      prev.map(notification => 
        notification.id === id 
          ? { ...notification, read: true }
          : notification
      )
    );
  }, []);

  // Î™®Îì† ÏïåÎ¶º ÏùΩÏùå Ï≤òÎ¶¨
  const markAllAsRead = useCallback(() => {
    setNotifications(prev => 
      prev.map(notification => ({ ...notification, read: true }))
    );
  }, []);

  // Î™®Îì† ÏïåÎ¶º ÏÇ≠Ï†ú
  const clearAll = useCallback(() => {
    setNotifications([]);
  }, []);

  // ÏÑ§Ï†ï ÏóÖÎç∞Ïù¥Ìä∏
  const updateSettings = useCallback((newSettings: Partial<NotificationSettings>) => {
    setSettings(prev => ({ ...prev, ...newSettings }));
  }, []);

  // ÏùΩÏßÄ ÏïäÏùÄ ÏïåÎ¶º Í∞úÏàò
  const unreadCount = notifications.filter(n => !n.read).length;

  const value: NotificationContextType = {
    notifications,
    settings,
    addNotification,
    removeNotification,
    markAsRead,
    markAllAsRead,
    clearAll,
    updateSettings,
    unreadCount
  };

  return (
    <NotificationContext.Provider value={value}>
      {children}
    </NotificationContext.Provider>
  );
}

// Ïª§Ïä§ÌÖÄ ÌõÖ
export function useNotifications() {
  const context = useContext(NotificationContext);
  if (!context) {
    throw new Error('useNotifications must be used within a NotificationProvider');
  }
  return context;
}
