'use client';

import React, { useState, useRef, useEffect } from 'react';
import { 
  Bell, 
  X, 
  Clock, 
  AlertCircle, 
  CheckCircle, 
  Info,
  Settings,
  Trash2,
  Check
} from 'lucide-react';
import { useNotifications } from './NotificationProvider';
import { NotificationItem } from './types';

/**
 * ÏïåÎ¶º ÎìúÎ°≠Îã§Ïö¥ Ïª¥Ìè¨ÎÑåÌä∏
 * Ïõπ ÌëúÏ§Ä Ìå®ÌÑ¥ÏùÑ Îî∞Îùº Íµ¨ÌòÑÎêú Ï†ëÍ∑ºÏÑ± ÎÜíÏùÄ ÏïåÎ¶º ÏÑºÌÑ∞
 */
export function NotificationDropdown() {
  const {
    notifications,
    unreadCount,
    markAsRead,
    markAllAsRead,
    removeNotification,
    clearAll
  } = useNotifications();

  const [isOpen, setIsOpen] = useState(false);
  const [filter, setFilter] = useState<'all' | 'unread'>('all');
  const dropdownRef = useRef<HTMLDivElement>(null);
  const buttonRef = useRef<HTMLButtonElement>(null);

  // Ïô∏Î∂Ä ÌÅ¥Î¶≠ Í∞êÏßÄ
  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (
        dropdownRef.current && 
        !dropdownRef.current.contains(event.target as Node) &&
        buttonRef.current &&
        !buttonRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false);
      }
    }

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  // ESC ÌÇ§Î°ú Îã´Í∏∞
  useEffect(() => {
    function handleEscapeKey(event: KeyboardEvent) {
      if (event.key === 'Escape' && isOpen) {
        setIsOpen(false);
        buttonRef.current?.focus();
      }
    }

    document.addEventListener('keydown', handleEscapeKey);
    return () => document.removeEventListener('keydown', handleEscapeKey);
  }, [isOpen]);

  // ÏïåÎ¶º ÏïÑÏù¥ÏΩò Î†åÎçîÎßÅ
  const getNotificationIcon = (type: NotificationItem['type']) => {
    const iconProps = { className: "w-4 h-4" };
    
    switch (type) {
      case 'success':
        return <CheckCircle {...iconProps} className="w-4 h-4 text-green-500" />;
      case 'warning':
        return <AlertCircle {...iconProps} className="w-4 h-4 text-yellow-500" />;
      case 'error':
        return <AlertCircle {...iconProps} className="w-4 h-4 text-red-500" />;
      default:
        return <Info {...iconProps} className="w-4 h-4 text-blue-500" />;
    }
  };

  // ÏãúÍ∞Ñ Ìè¨Îß∑ÌåÖ
  const formatTime = (timestamp: Date) => {
    const now = new Date();
    const diff = now.getTime() - timestamp.getTime();
    const minutes = Math.floor(diff / 60000);
    
    if (minutes < 1) return 'Î∞©Í∏à Ï†Ñ';
    if (minutes < 60) return `${minutes}Î∂Ñ Ï†Ñ`;
    
    const hours = Math.floor(minutes / 60);
    if (hours < 24) return `${hours}ÏãúÍ∞Ñ Ï†Ñ`;
    
    const days = Math.floor(hours / 24);
    return `${days}Ïùº Ï†Ñ`;
  };

  // ÌïÑÌÑ∞ÎßÅÎêú ÏïåÎ¶º Î™©Î°ù
  const filteredNotifications = notifications.filter(notification => {
    if (filter === 'unread') return !notification.read;
    return true;
  });

  // ÏïåÎ¶º ÌÅ¥Î¶≠ Ìï∏Îì§Îü¨
  const handleNotificationClick = (notification: NotificationItem) => {
    if (!notification.read) {
      markAsRead(notification.id);
    }
  };

  return (
    <div className="relative">
      {/* ÏïåÎ¶º Î≤ÑÌäº */}
      <button
        ref={buttonRef}
        onClick={() => setIsOpen(!isOpen)}
        className="
          relative p-2 rounded-lg 
          bg-gray-100 dark:bg-gray-700 
          text-gray-600 dark:text-gray-400
          hover:bg-gray-200 dark:hover:bg-gray-600 
          hover:text-gray-900 dark:hover:text-gray-100
          focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2
          transition-all duration-200
        "
        aria-label={`ÏïåÎ¶º ${unreadCount > 0 ? `(${unreadCount}Í∞úÏùò ÏùΩÏßÄ ÏïäÏùÄ ÏïåÎ¶º)` : ''}`}
        aria-expanded={isOpen}
        aria-haspopup="true"
      >
        <Bell className="w-5 h-5" />
        
        {/* ÏùΩÏßÄ ÏïäÏùÄ ÏïåÎ¶º Î∞∞ÏßÄ */}
        {unreadCount > 0 && (
          <span className="
            absolute -top-1 -right-1 
            min-w-[18px] h-[18px] 
            bg-red-500 text-white 
            text-xs font-bold 
            rounded-full 
            flex items-center justify-center
          ">
            {unreadCount > 99 ? '99+' : unreadCount}
          </span>
        )}
      </button>

      {/* ÎìúÎ°≠Îã§Ïö¥ Î©îÎâ¥ */}
      {isOpen && (
        <div
          ref={dropdownRef}
          className="
            absolute right-0 mt-2 
            w-80 max-h-96 
            bg-white dark:bg-gray-800 
            border border-gray-200 dark:border-gray-700 
            rounded-lg shadow-lg 
            z-50 overflow-hidden
          "
          role="menu"
          aria-labelledby="notification-menu"
        >
          {/* Ìó§Îçî */}
          <div className="p-4 border-b border-gray-200 dark:border-gray-700">
            <div className="flex items-center justify-between mb-3">
              <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                ÏïåÎ¶º
              </h3>
              <button
                onClick={() => setIsOpen(false)}
                className="
                  p-1 rounded-md 
                  text-gray-400 hover:text-gray-600 dark:hover:text-gray-200
                  hover:bg-gray-100 dark:hover:bg-gray-700
                  focus:outline-none focus:ring-2 focus:ring-blue-500
                "
                aria-label="ÏïåÎ¶º Ìå®ÎÑê Îã´Í∏∞"
              >
                <X className="w-4 h-4" />
              </button>
            </div>

            {/* ÌïÑÌÑ∞ Î∞è Ïï°ÏÖò Î≤ÑÌäº */}
            <div className="flex items-center justify-between">
              <div className="flex space-x-2">
                <button
                  onClick={() => setFilter('all')}
                  className={`
                    px-3 py-1 text-sm rounded-md transition-colors
                    ${filter === 'all' 
                      ? 'bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-300' 
                      : 'text-gray-600 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-700'
                    }
                  `}
                >
                  Ï†ÑÏ≤¥ ({notifications.length})
                </button>
                <button
                  onClick={() => setFilter('unread')}
                  className={`
                    px-3 py-1 text-sm rounded-md transition-colors
                    ${filter === 'unread' 
                      ? 'bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-300' 
                      : 'text-gray-600 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-700'
                    }
                  `}
                >
                  ÏùΩÏßÄ ÏïäÏùå ({unreadCount})
                </button>
              </div>

              <div className="flex items-center space-x-1">
                {unreadCount > 0 && (
                  <button
                    onClick={markAllAsRead}
                    className="
                      p-1 rounded-md 
                      text-gray-400 hover:text-blue-600 dark:hover:text-blue-400
                      hover:bg-gray-100 dark:hover:bg-gray-700
                      focus:outline-none focus:ring-2 focus:ring-blue-500
                    "
                    title="Î™®Îëê ÏùΩÏùå Ï≤òÎ¶¨"
                  >
                    <Check className="w-4 h-4" />
                  </button>
                )}
                
                {notifications.length > 0 && (
                  <button
                    onClick={clearAll}
                    className="
                      p-1 rounded-md 
                      text-gray-400 hover:text-red-600 dark:hover:text-red-400
                      hover:bg-gray-100 dark:hover:bg-gray-700
                      focus:outline-none focus:ring-2 focus:ring-red-500
                    "
                    title="Î™®Îì† ÏïåÎ¶º ÏÇ≠Ï†ú"
                  >
                    <Trash2 className="w-4 h-4" />
                  </button>
                )}
              </div>
            </div>
          </div>

          {/* ÏïåÎ¶º Î™©Î°ù */}
          <div className="max-h-64 overflow-y-auto">
            {filteredNotifications.length === 0 ? (
              <div className="p-8 text-center">
                <Bell className="w-12 h-12 mx-auto mb-3 text-gray-300 dark:text-gray-600" />
                <p className="text-gray-500 dark:text-gray-400">
                  {filter === 'unread' ? 'ÏùΩÏßÄ ÏïäÏùÄ ÏïåÎ¶ºÏù¥ ÏóÜÏäµÎãàÎã§' : 'ÏïåÎ¶ºÏù¥ ÏóÜÏäµÎãàÎã§'}
                </p>
              </div>
            ) : (
              filteredNotifications.map((notification) => (
                <div
                  key={notification.id}
                  onClick={() => handleNotificationClick(notification)}
                  className={`
                    p-4 border-b border-gray-100 dark:border-gray-700 last:border-b-0
                    hover:bg-gray-50 dark:hover:bg-gray-750 cursor-pointer
                    transition-colors duration-150
                    ${!notification.read ? 'bg-blue-50 dark:bg-blue-900/20' : ''}
                  `}
                  role="menuitem"
                >
                  <div className="flex items-start space-x-3">
                    <div className="flex-shrink-0 mt-1">
                      {getNotificationIcon(notification.type)}
                    </div>
                    
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center justify-between mb-1">
                        <h4 className="text-sm font-medium text-gray-900 dark:text-white truncate">
                          {notification.title}
                        </h4>
                        
                        {!notification.read && (
                          <div className="w-2 h-2 bg-blue-500 rounded-full flex-shrink-0 ml-2" />
                        )}
                      </div>
                      
                      <p className="text-sm text-gray-600 dark:text-gray-300 mb-2 line-clamp-2">
                        {notification.message}
                      </p>
                      
                      <div className="flex items-center justify-between">
                        <span className="text-xs text-gray-400 dark:text-gray-500 flex items-center">
                          <Clock className="w-3 h-3 mr-1" />
                          {formatTime(notification.timestamp)}
                        </span>
                        
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            removeNotification(notification.id);
                          }}
                          className="
                            p-1 rounded-md 
                            text-gray-400 hover:text-red-600 dark:hover:text-red-400
                            hover:bg-gray-100 dark:hover:bg-gray-700
                            focus:outline-none focus:ring-2 focus:ring-red-500
                          "
                          title="ÏïåÎ¶º ÏÇ≠Ï†ú"
                        >
                          <X className="w-3 h-3" />
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>

          {/* Ìë∏ÌÑ∞ */}
          {notifications.length > 0 && (
            <div className="p-3 border-t border-gray-200 dark:border-gray-700">
              <button
                onClick={() => {
                  setIsOpen(false);
                  // Ïó¨Í∏∞Ïóê ÏÑ§Ï†ï ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌïòÎäî Î°úÏßÅ Ï∂îÍ∞Ä
                }}
                className="
                  w-full px-3 py-2 text-sm
                  text-blue-600 dark:text-blue-400
                  hover:bg-blue-50 dark:hover:bg-blue-900/20
                  rounded-md transition-colors
                  flex items-center justify-center
                "
              >
                <Settings className="w-4 h-4 mr-2" />
                ÏïåÎ¶º ÏÑ§Ï†ï
              </button>
            </div>
          )}
        </div>
      )}
    </div>
  );
}
